* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

.search {
  border-radius: 10px;
  border-style: solid;
  border-width: 1px;
  max-width: 75%;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
  padding-left: 1rem;
}

ul {
  padding: 3rem 0;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 0 10px 10px 0;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
  max-width: fit-content;
  margin: 5px auto;
}

.card img {
  max-height: 170px;
}
